import Cookies from 'js-cookie'

const TokenKey = 'terminal-Token'



export function getToken() {
  	return Cookies.get(TokenKey)
}

export function setToken(token) {
  	return Cookies.set(TokenKey, token,{expires:30*60})
}

export function removeToken() {
  	return Cookies.remove(TokenKey)
}


/**
 * 取用户对象 UserInfo
 */
export function getUserInfo() {
    var localInfoStorage = window.localStorage
    var userInfo = JSON.parse(localInfoStorage.IBM_UserInfo || '{}')
    return userInfo
}

/**
 * 设置用户对象 UserInfo
 */
export function setUserInfo(userInfo) {
    var localInfoStorage = window.localStorage
    localInfoStorage.IBM_UserInfo = JSON.stringify(userInfo || {})
}

/**
 * 删除用户对象 UserInfo
 */
export function removeUserInfo(userInfo) {
    var localInfoStorage = window.localStorage
    localInfoStorage.IBM_UserInfo.remove({key:userInfo})
}







